<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  import {get_cropid, get_projid, get_model, get_province, get_city, get_user, get_uid, get_curl, get_seat, get_pmemo, get_project, get_brand, get_provinces, get_citys} from '@/api/index'
export default {
  name: 'App',
  created(){
    this.$nextTick(function(){
      get_cropid(); //获取外呼团队
      get_projid(); //获取品牌名称
      get_model();  //获取数据批次
      get_province(); //获取外呼团队
      get_city(); //获取品牌名称
      get_user();  //获取坐席姓名
      get_uid();   //不成单原因匹配数据
      get_curl();
      get_seat();
      get_pmemo();      //提供比例获取品牌名称
      get_project();     //提供比例获取品牌名称
      get_brand();
      get_provinces();
      get_citys();
    })
  }
}
</script>

<style>

</style>



