import Vue from 'vue'
import ajax from '@/config/ajax'
import {format} from '@/config/util'

//数据批次饼状图
export function get_dataname(calldate,modelName,corp,proj) {
  return ajax.request({
    methods: 'get',
    url: 'refuse',
    params:{
      calldate:calldate,
      corp:corp,
      proj:proj,
      modelname:modelName
    }
  });
}

//获取外呼团队id及名称
export function get_cropid(){
  return ajax.request({
    methods: 'get',
    url: 'public',
    params:{
      type:'corp'
    }
  }).then((res) => {
    if(res.code == '200'){
      var data ={
        id: 0,
        memo: '全部'
      }
      res.data.unshift(data);
      localStorage.setItem('cropid', JSON.stringify(res.data));
    }

  }).catch((error) => {
    console.log(error)
  })
}

