//获取品牌id及名称  字典
export function get_brand(){
  return ajax.request({
    method: 'post',
    url: 'public',
    params:{
      type:"brand"
    }
  }).then(res => {
    if(res.code == '200'){
      res.data[0] = '全部';
      localStorage.setItem('brand', JSON.stringify(res.data));
    }
  }).catch((error) => {
    console.log(error);
  })
}


//获取数据批次
export function get_model(){
  return ajax.request({
    methods: 'get',
    url: 'public',
    params:{
      type:'model'
    }
  }).then((res) => {
    if(res.code == '200'){
      var data =[{
        id: -1,
        memo: '全部'
      },{
        id: 0,
        memo: 'unknown'
      }];
     // Vue.set(res.data, 0, data);
      var dataList = data.concat(res.data);
      res.data.unshift(dataList);
      localStorage.setItem('model', JSON.stringify(dataList));
    }
  }).catch((error) => {
    console.log(error);
  })
}

